.u-aspect-ratio {
  display: block;
  position: relative;
  overflow: hidden;

  &.is-1x1 {
    padding-top: $aspect-ratio-1x1;
  }

  &.is-2x1 {
    padding-top: $aspect-ratio-2x1;
  }

  &.is-4x3 {
    padding-top: $aspect-ratio-4x3;
  }

  &.is-6x4 {
    padding-top: $aspect-ratio-6x4;
  }

  &.is-16x9 {
    padding-top: $aspect-ratio-16x9;
  }

  &.is-1x2 {
    padding-top: $aspect-ratio-1x2;
  }

  &.is-3x4 {
    padding-top: $aspect-ratio-3x4;
  }

  &.is-4x6 {
    padding-top: $aspect-ratio-4x6;
  }

  &.is-9x16 {
    padding-top: $aspect-ratio-9x16;
  }

  iframe {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  video {
    // Make video at least 100% wide and tall
    min-width: 100%;
    min-height: 100%;

    // Setting width & height to auto prevents the browser from stretching or squishing the video
    width: auto;
    height: auto;

    // Center the video
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
